#root{
  width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.box_nav {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 195px;
  height: 110px;
  margin-right: 10px;
  margin-left: 10px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 10px;
  background-color: #f4f4f4;
  text-decoration: none;
  color: #909090;
}
.box_nav:hover {
  background-color: rgb(211, 211, 211);
}
.KundenListItem:hover {
  background-color: #f4f4f4;
  cursor: pointer;
}
.navigation {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.nav-item.active {
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: #099;
}
.active {
  background-color: #c272d9;
  color: white;
  font-weight: 600;
}
@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.items{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1450px;
  margin: 0 125px;
  padding: 0;
  justify-content: space-evenly;

}
.items .item{
  list-style: none;
  margin: 10px;
}

.challenges .week{
  padding-left: 0;
}
.challenges li{
  list-style: none;
}
.challenges .challenge{
  display: flex;
  justify-content: space-between;
  margin: 10px;
  padding: 5px;
}

.stats div p.statsline span{
  width: 75px;
  text-align: center;
  margin: 0px;
}
.stats div p.headline span{
  width: 150px;
  text-align: center;
  padding-bottom: 5px;
  margin: 0px;
}