.body {
    padding-top: 35px;
    padding-right: 50px;
    padding-left: 50px;
    background-color: #f4f4f4;
    padding-bottom: 0px;
    height: 100%;
  }
  
  .main {
    width: 100%;
    min-height: 100%;
    padding-top: 10px;
    padding-right: 50px;
    padding-left: 50px;
    border-radius: 24px 24px 0px 0px;
    background-color: #fff;
  }
  
  .nav-item {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 195px;
    height: 110px;
    margin-right: 10px;
    margin-left: 10px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    border-radius: 10px;
    background-color: #f4f4f4;
  }
  
  .nav-item.active {
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #099;
  }
  
  .navigation {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
  }
  
  .left-navigation {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
  
  .image {
    width: 18px;
  }
  
  .image-2 {
    width: 22px;
  }
  
  .image-3 {
    width: 31px;
  }
  
  .image-4 {
    width: 25px;
  }
  
  .navigation-heading {
    margin-top: 9px;
    font-family: 'Open Sans', sans-serif;
    color: #909090;
    font-weight: 600;
    letter-spacing: 0px;
    text-transform: uppercase;
  }
  
  .navigation-heading.active {
    color: #fff;
  }
  
  .image-5 {
    width: 226px;
  }